var sty = sty || {};

sty.stickyHeader = function(el) {

    var $window = $(window),
        header = el;
        content = $('.js_stickyContent');

    function stickyHeader() {

        if ($(window).width() > 1024 && $window.scrollTop() > 40) {

            header.addClass('is_small');
            content.addClass('is_small');

        } else {

            header.removeClass('is_small');
            content.removeClass('is_small');

        }
    }

    $window.on('scroll', function() {

        stickyHeader();

    });

    $window.on('resize', function() {

        stickyHeader();

    });

    stickyHeader();

};

$(document).ready(function() {

    $('.js_stickyHeader').each(function() {

        new sty.stickyHeader($(this));

    });

});