var sty = sty || {};

sty.cookiePolicy = function() {

    var cookie = Cookies.get('zrin-otkup'),
        cookieMessage = $('.js_cookie');

    if ( typeof cookie === 'undefined' ) {

        setTimeout(function(){

            cookieMessage.addClass('cookie--open');       
 
        }, 1000);        
    } 

    cookieMessage.on(sty.click_touchstart, function(evt) {

        evt.preventDefault();

        $(this).removeClass('cookie--open');
        Cookies.set('zrin-otkup', 'zrin', { expires: 365 });

    });
    
};

$(document).ready(function (){

    sty.cookiePolicy();

});
