var sty = sty || {};

sty.navigation = function() {

	var navTrigger = $('.js_navTrigger'),
        hamburger = $('.js_hamburger'),
        fog = $('.js_navigationFog'),
        navList = $('.js_navigation'),
        flag = false;
    
    //navTrigger.on(sty.click_touchstart, function (evt) {
    navTrigger.on('touchstart click', function (evt) {

        evt.preventDefault();
        evt.stopPropagation();

        if (!flag) {

            flag = true;            
            setTimeout(function(){ flag = false; }, 100);

            navList.toggleClass('is_active');
            fog.toggleClass('is_active');
            hamburger.toggleClass('open');
        }

        return false
        
    });

    //fog.on(sty.click_touchstart, function (evt) {
    fog.on('touchstart click', function (evt) {

        evt.preventDefault();
        evt.stopPropagation();

        if (!flag) {

            flag = true;            
            setTimeout(function(){ flag = false; }, 100);

            navList.removeClass('is_active');
            fog.removeClass('is_active');
            hamburger.removeClass('open');
        }

        return false
        
    });
};

$(document).ready(function (){

	sty.navigation();

});
